@charset "UTF-8";
body {
  margin: 0 auto;
  padding: 0; }

.logo {
  display: flex;
  align-self: center;
  margin-top: 18px;
  margin-bottom: 18px;
  padding: 2px 25px 0 25px;
  width: 100px;
  background-color: #ffffff;
  /*убрать*/ }

.page-header__wrapper {
  max-width: 1200px;
  min-height: 99px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  background-color: red;
  /*убрать*/ }

.main-nav {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end; }

.main-nav__list {
  display: flex; }

.site-list {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style: none; }

.site-list__link {
  display: block;
  padding-top: 35px;
  padding-bottom: 35px;
  padding-left: 45px;
  padding-right: 45px;
  text-decoration: none; }

.info-contacts {
  display: flex; }

.info-contacts__tel {
  display: block;
  padding-top: 35px;
  padding-bottom: 35px;
  padding-left: 10px;
  padding-right: 10px; }
